<template>
  <div>
    <ul two-line>
      <template v-for="(survey, index) in surveys">
        <li :key="index">
          <template>
            <p class="header to-uppercase" v-text="survey.title"></p>
          </template>
        </li>
        <v-divider v-if="index < surveys.length - 1" :key="index"></v-divider>
      </template>
    </ul>
  </div>
</template>
<script>
export default {
  name: "surveys",
  data() {
    return {
      surveys: [],
    };
  },
  mounted() {
    this.axios.get(this.api.ACTIONS.SURVEY).then((res) => {
      this.surveys = res.data.data.data;
      console.log(this.surveys);
    });
  },
};
</script>
